import { useCallback, useEffect, useState } from "react";
import { toast } from "react-hot-toast";
import { API_ENDPOINTS } from "../config/api";
import useApi from "./useApi";

/**
 * Hook personnalis pour grer les employs
 */
const useEmployees = () => {
  const [employees, setEmployees] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const api = useApi();

  /**
   * Rcupre tous les employs
   */
  const fetchEmployees = useCallback(async () => {
    if (!loading) setLoading(true);
    try {
      const response = await api.get(API_ENDPOINTS.EMPLOYEES.BASE);

      if (response.ok) {
        // S'assurer que les donnes sont un tableau
        const employeesData = Array.isArray(response.data) ? response.data : [];
        setEmployees(employeesData);
        setError(null);
      } else {
        throw new Error(
          response.data?.message || "Erreur lors du chargement des employs"
        );
      }
    } catch (err) {
      console.error("Erreur lors du chargement des employs:", err);
      setError("Erreur lors du chargement des employs");
      toast.error("Erreur lors du chargement des employs");
      // En cas d'erreur, s'assurer que employees reste un tableau vide
      setEmployees([]);
    } finally {
      setLoading(false);
    }
  }, [api, loading]);

  /**
   * Rcupre un employ par son ID
   */
  const fetchEmployeeById = useCallback(
    async (id) => {
      setLoading(true);
      setError(null);

      try {
        const response = await api.get(API_ENDPOINTS.EMPLOYEES.BY_ID(id));

        if (response.ok) {
          return response.data;
        } else {
          throw new Error(
            response.data?.message ||
              "Erreur lors de la rcupration de l'employ"
          );
        }
      } catch (err) {
        console.error(
          `Erreur lors de la rcupration de l'employ #${id}:`,
          err
        );
        setError(err.message || "Erreur lors de la rcupration de l'employ");
        toast.error("Erreur lors de la rcupration de l'employ");
        return null;
      } finally {
        setLoading(false);
      }
    },
    [api]
  );

  /**
   * Cre un nouvel employ
   */
  const createEmployee = useCallback(
    async (employeeData) => {
      try {
        console.log("Donnes envoyes  l'API pour cration:", employeeData);

        // Convertir les valeurs numriques
        const formattedData = {
          ...employeeData,
          contractHours: parseFloat(employeeData.contractHours) || 35,
          hourlyRate: parseFloat(employeeData.hourlyRate) || 0,
        };

        const response = await api.post(
          API_ENDPOINTS.EMPLOYEES.BASE,
          formattedData
        );

        console.log("Rponse de l'API pour cration:", response);

        // Vrifier si la rponse contient un indicateur de succs
        if (response && response.success === false) {
          console.error(
            "Erreur API:",
            response.message || "Erreur lors de la cration de l'employ"
          );
          return {
            success: false,
            error:
              response.message || "Erreur lors de la cration de l'employ",
          };
        }

        // Si la rponse contient un employ, c'est un succs
        if (response && response.employee) {
          // Mettre  jour l'tat local
          setEmployees((prev) => [...prev, response.employee]);
          return { success: true, employee: response.employee };
        }

        // Si la rponse est un objet mais ne contient pas d'employ, vrifier s'il y a un message d'erreur
        if (response && typeof response === "object") {
          if (response.message && response.message.includes("erreur")) {
            return { success: false, error: response.message };
          }

          // Si la rponse est l'employ lui-mme
          if (response.id) {
            setEmployees((prev) => [...prev, response]);
            return { success: true, employee: response };
          }
        }

        // Fallback pour les anciennes API qui renvoient directement l'employ
        setEmployees((prev) => [...prev, response]);
        return { success: true, employee: response };
      } catch (err) {
        console.error("Erreur lors de la cration de l'employ:", err);

        // Extraire un message d'erreur plus prcis si possible
        let errorMessage = "Erreur lors de la cration de l'employ";

        if (err.response && err.response.data) {
          errorMessage = err.response.data.message || errorMessage;
        } else if (err.message) {
          errorMessage = err.message;
        }

        return { success: false, error: errorMessage };
      }
    },
    [api]
  );

  /**
   * Met  jour un employ existant
   */
  const updateEmployee = useCallback(
    async (id, employeeData) => {
      try {
        if (!id) {
          console.error("ID d'employ non valide:", id);
          return { success: false, error: "ID d'employ non valide" };
        }

        console.log("Donnes envoyes  l'API pour mise  jour:", employeeData);

        // Convertir les valeurs numriques
        const formattedData = {
          ...employeeData,
          contractHours: parseFloat(employeeData.contractHours) || 35,
          hourlyRate: parseFloat(employeeData.hourlyRate) || 0,
        };

        const response = await api.put(
          API_ENDPOINTS.EMPLOYEES.BY_ID(id),
          formattedData
        );

        console.log("Rponse de l'API pour mise  jour:", response);

        if (!response.ok) {
          const errorMessage =
            response.data?.message ||
            "Erreur lors de la mise  jour de l'employ";
          console.error("Erreur API:", errorMessage);
          return { success: false, error: errorMessage };
        }

        // Mettre  jour l'tat local
        setEmployees((prev) =>
          prev.map((emp) =>
            emp.id === id ? { ...emp, ...response.data } : emp
          )
        );

        return { success: true, employee: response.data };
      } catch (err) {
        console.error("Erreur lors de la mise  jour de l'employ:", err);
        return { success: false, error: err.message || "Erreur inconnue" };
      }
    },
    [api]
  );

  /**
   * Supprime un employ
   */
  const deleteEmployee = useCallback(
    async (id) => {
      try {
        if (!id) {
          console.error("ID d'employ non valide pour suppression:", id);
          return { success: false, error: "ID d'employ non valide" };
        }

        console.log("Tentative de suppression de l'employ avec ID:", id);

        const response = await api.delete(API_ENDPOINTS.EMPLOYEES.BY_ID(id));

        console.log("Rponse de l'API pour suppression:", response);

        // Vrifier si la rponse contient success: true
        if (response && response.success === true) {
          // Mettre  jour l'tat local
          setEmployees((prev) => prev.filter((emp) => emp.id !== id));
          return { success: true };
        } else {
          const errorMessage =
            response?.message || "Erreur lors de la suppression de l'employ";
          console.error("Erreur API:", errorMessage);
          return { success: false, error: errorMessage };
        }
      } catch (err) {
        console.error("Erreur lors de la suppression de l'employ:", err);
        return { success: false, error: err.message || "Erreur inconnue" };
      }
    },
    [api]
  );

  /**
   * Filtre les employs par statut
   * @param {string|null} status - Le statut  filtrer (active, inactive, vacation, sick) ou null pour tous
   * @returns {Array} - Les employs filtrs
   */
  const getEmployeesByStatus = useCallback(
    (status) => {
      if (!status || status === "all") return employees;
      return employees.filter((employee) => employee.status === status);
    },
    [employees]
  );

  /**
   * Rcupre le solde d'heures d'un employ
   */
  const fetchEmployeeHourBalance = useCallback(
    async (id) => {
      try {
        const response = await api.get(`/api/hour-balance/${id}`);

        if (response.ok) {
          // Mettre  jour l'tat local des employs avec le nouveau solde d'heures
          setEmployees((prev) =>
            prev.map((emp) =>
              emp.id === id
                ? { ...emp, hour_balance: response.data.hour_balance }
                : emp
            )
          );
          return response.data.hour_balance;
        } else {
          console.warn(
            `Avertissement: Impossible de rcuprer le solde d'heures pour l'employ #${id}:`,
            response.data?.message || "Raison inconnue"
          );
          return 0; // Retourner 0 par dfaut au lieu de lancer une erreur
        }
      } catch (err) {
        console.warn(
          `Avertissement: Erreur lors de la rcupration du solde d'heures pour l'employ #${id}:`,
          err
        );
        // Ne pas afficher de toast d'erreur pour ne pas perturber l'utilisateur
        return 0; // Retourner 0 par dfaut
      }
    },
    [api]
  );

  /**
   * Rcupre le solde d'heures pour tous les employs
   */
  const fetchAllEmployeesHourBalances = useCallback(async () => {
    try {
      const promises = employees.map((employee) =>
        fetchEmployeeHourBalance(employee.id)
      );

      await Promise.all(promises);
    } catch (err) {
      console.error("Erreur lors de la rcupration des soldes d'heures:", err);
    }
  }, [employees, fetchEmployeeHourBalance]);

  // Charger les employs au montage du composant
  useEffect(() => {
    let mounted = true;
    let retryCount = 0;
    const maxRetries = 3;

    const loadEmployees = async () => {
      if (retryCount >= maxRetries) {
        if (mounted) {
          setError(
            "Erreur lors du chargement des employs aprs plusieurs tentatives"
          );
          setLoading(false);
        }
        return;
      }

      try {
        console.log("Chargement des employs...");
        const token = localStorage.getItem("token");

        if (!token) {
          console.error("Token d'authentification manquant");
          setError("Vous devez tre connect pour accder  ces donnes");
          setLoading(false);
          return;
        }

        const data = await api.get(API_ENDPOINTS.EMPLOYEES.BASE);
        console.log("Donnes des employs reues:", data);

        if (mounted) {
          if (Array.isArray(data)) {
            setEmployees(data);
            setError(null);
          } else {
            console.error("Format de donnes invalide:", data);
            setError("Format de donnes invalide");
          }
          setLoading(false);
        }
      } catch (err) {
        if (mounted) {
          console.error("Erreur lors du chargement des employs:", err);
          setError(err.message || "Erreur lors du chargement des employs");

          // Ressayer avec un dlai exponentiel
          retryCount++;
          setTimeout(loadEmployees, 1000 * Math.pow(2, retryCount));
        }
      }
    };

    loadEmployees();

    return () => {
      mounted = false;
    };
  }, [api]);

  // Charger les soldes d'heures aprs avoir charg les employs
  useEffect(() => {
    if (employees.length > 0) {
      fetchAllEmployeesHourBalances();
    }
  }, [employees.length, fetchAllEmployeesHourBalances]);

  return {
    employees,
    loading,
    error,
    fetchEmployees,
    fetchEmployeeById,
    createEmployee,
    updateEmployee,
    deleteEmployee,
    getEmployeesByStatus,
    fetchEmployeeHourBalance,
    fetchAllEmployeesHourBalances,
  };
};

export default useEmployees;
