import { useCallback, useMemo } from "react";
import { toast } from "react-hot-toast";
import { API_URL } from "../config/api";

/**
 * Hook personnalis pour effectuer des appels API
 * @returns {Object} Mthodes pour effectuer des requtes API
 */
const useApi = () => {
  const handleResponse = useCallback(async (response) => {
    try {
      // Rcuprer les en-ttes pour le dbogage
      const headers = {};
      response.headers.forEach((value, key) => {
        headers[key] = value;
      });

      console.log("Rponse du serveur:", {
        url: response.url,
        status: response.status,
        statusText: response.statusText,
        headers: headers,
      });

      // Vrifier si la rponse est au format JSON
      const contentType = response.headers.get("content-type");
      const isJson = contentType && contentType.includes("application/json");

      // Rcuprer le corps de la rponse
      let data;
      if (isJson) {
        data = await response.json();
        console.log("Donnes JSON reues:", data);
      } else {
        const text = await response.text();
        console.warn("Rponse non-JSON reue:", text);
        try {
          // Essayer de parser le texte comme JSON
          data = JSON.parse(text);
          console.log("Texte pars comme JSON:", data);
        } catch (e) {
          data = { message: text };
        }
      }

      // Grer les diffrents codes de statut
      if (response.ok) {
        return data;
      } else {
        // Grer les erreurs d'authentification (401, 403)
        if (response.status === 401 || response.status === 403) {
          console.error("Erreur d'authentification:", data);
          toast.error(
            "Session expire ou accs non autoris. Veuillez vous reconnecter."
          );

          // Supprimer le token et l'utilisateur du localStorage
          localStorage.removeItem("token");
          localStorage.removeItem("user");

          // Rediriger vers la page de connexion aprs un dlai
          setTimeout(() => {
            window.location.href = "/login";
          }, 2000);
        }

        // Grer les erreurs serveur (500)
        if (response.status === 500) {
          console.error("Erreur serveur:", data);
          console.error("URL:", response.url);
          console.error("Mthode:", response.method);

          // Journaliser plus de dtails pour le dbogage
          if (data.error) {
            console.error("Dtails de l'erreur:", data.error);
          }
          if (data.stack) {
            console.error("Stack trace:", data.stack);
          }
        }

        // Construire un message d'erreur dtaill
        const errorMessage =
          data.message ||
          data.error ||
          response.statusText ||
          "Erreur inconnue";
        const error = new Error(errorMessage);
        error.status = response.status;
        error.response = { status: response.status, data };

        throw error;
      }
    } catch (error) {
      console.error("Erreur lors du traitement de la rponse:", error);
      throw error;
    }
  }, []);

  const api = useMemo(() => {
    const get = async (endpoint) => {
      try {
        console.log(`[API] GET ${endpoint}`);
        const token = localStorage.getItem("token");

        if (!token) {
          console.error(
            "Token d'authentification manquant pour la requte GET"
          );
          toast.error("Vous devez tre connect pour accder  ces donnes");
          setTimeout(() => {
            window.location.href = "/login";
          }, 2000);
          return { ok: false, status: 401, data: [] };
        }

        const response = await fetch(`${API_URL}${endpoint}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          credentials: "include",
        });

        return await handleResponse(response);
      } catch (error) {
        console.error(`[API] GET ${endpoint} Error:`, error);

        // Si l'erreur est lie  l'authentification, rediriger vers la page de connexion
        if (error.status === 401 || error.status === 403) {
          toast.error("Session expire. Veuillez vous reconnecter.");
          setTimeout(() => {
            window.location.href = "/login";
          }, 2000);
        } else {
          toast.error(
            error.message || "Erreur lors de la rcupration des donnes"
          );
        }

        throw error;
      }
    };

    // Fonction utilitaire pour convertir camelCase en snake_case
    const camelToSnakeCase = (str) => {
      return str.replace(/[A-Z]/g, (letter) => `_${letter.toLowerCase()}`);
    };

    const post = async (endpoint, data) => {
      try {
        // Vrifier que les donnes sont valides
        if (!data || typeof data !== "object") {
          console.error("Donnes invalides pour la requte POST:", data);
          throw new Error("Donnes invalides pour la requte POST");
        }

        // Convertir les donnes en snake_case pour le backend
        const snakeCaseData = {};
        for (const key in data) {
          snakeCaseData[camelToSnakeCase(key)] = data[key];
        }

        // Rcuprer le token d'authentification
        const token = localStorage.getItem("token");
        if (!token) {
          console.error("Token d'authentification manquant");
          throw new Error(
            "Vous devez tre connect pour effectuer cette action"
          );
        }

        // Configurer les en-ttes de la requte
        const headers = {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        };

        // Journaliser les dtails de la requte (sans le token complet)
        console.log("Dtails de la requte POST:", {
          endpoint,
          dataSize: JSON.stringify(snakeCaseData).length,
          headers: { ...headers, Authorization: "Bearer [MASQU]" },
        });

        // Effectuer la requte avec un timeout
        const controller = new AbortController();
        const timeoutId = setTimeout(() => controller.abort(), 30000); // 30 secondes de timeout

        const response = await fetch(`${API_URL}${endpoint}`, {
          method: "POST",
          headers,
          body: JSON.stringify(snakeCaseData),
          signal: controller.signal,
        });

        // Annuler le timeout
        clearTimeout(timeoutId);

        // Traiter la rponse
        return handleResponse(response);
      } catch (error) {
        // Grer les erreurs spcifiques
        if (error.name === "AbortError") {
          console.error("La requte a t interrompue (timeout):", error);
          throw new Error(
            "La requte a pris trop de temps, veuillez ressayer"
          );
        }

        if (
          error.message.includes("NetworkError") ||
          error.message.includes("Failed to fetch")
        ) {
          console.error("Erreur rseau lors de la requte POST:", error);
          throw new Error(
            "Problme de connexion au serveur, veuillez vrifier votre connexion internet"
          );
        }

        // Journaliser et propager l'erreur
        console.error("Erreur lors de la requte POST:", error);
        throw error;
      }
    };

    const put = async (endpoint, data) => {
      try {
        console.log(`[API] PUT ${endpoint}`, data);
        const token = localStorage.getItem("token");

        // S'assurer que les donnes sont srialisables
        const cleanData = JSON.parse(JSON.stringify(data));

        const response = await fetch(`${API_URL}${endpoint}`, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: token ? `Bearer ${token}` : "",
          },
          body: JSON.stringify(cleanData),
          credentials: "include",
        });

        const result = await handleResponse(response);
        console.log(`[API] PUT ${endpoint} Response:`, result);
        return result;
      } catch (error) {
        console.error(`[API] PUT ${endpoint} Error:`, error);
        return {
          ok: false,
          status: 0,
          data: { message: error.message || "Erreur lors de la requte PUT" },
          headers: new Headers(),
        };
      }
    };

    const del = async (endpoint) => {
      try {
        console.log(`[API] DELETE ${endpoint}`);
        const token = localStorage.getItem("token");
        const response = await fetch(`${API_URL}${endpoint}`, {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
            Authorization: token ? `Bearer ${token}` : "",
          },
          credentials: "include",
        });

        const result = await handleResponse(response);
        console.log(`[API] DELETE ${endpoint} Response:`, result);
        return result;
      } catch (error) {
        console.error(`[API] DELETE ${endpoint} Error:`, error);
        return {
          ok: false,
          status: 0,
          data: {
            message: error.message || "Erreur lors de la requte DELETE",
          },
          headers: new Headers(),
        };
      }
    };

    return {
      get,
      post,
      put,
      delete: del,
    };
  }, [handleResponse]);

  return api;
};

export default useApi;
